/* Telegram-style Main Page */
.main-page {
  min-height: 100vh;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  background-color: var(--tg-theme-bg-color);
  transition: background-color 0.2s;
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}

/* Header */
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  padding-top: max(12px, env(safe-area-inset-top));
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
  background-color: var(--tg-header-bg);
  border-bottom: 1px solid var(--tg-card-border);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.header-left {
  display: flex;
  align-items: center;
}

.readme-link {
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--tg-theme-hint-color);
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.15s ease;
  background: transparent;
  border: none;
  cursor: pointer;
  font-family: inherit;
}

.readme-link:hover {
  color: var(--tg-theme-accent-color);
  background-color: var(--tg-theme-secondary-bg-color);
}

.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.lang-toggle {
  padding: 6px 12px;
  background: transparent;
  border: 1.5px solid var(--tg-card-border);
  border-radius: 8px;
  font-size: 13px;
  font-weight: 600;
  color: var(--tg-theme-hint-color);
  cursor: pointer;
  transition: all 0.15s ease;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.lang-toggle:hover {
  color: var(--tg-theme-accent-color);
  border-color: var(--tg-theme-accent-color);
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 24px;
  padding-bottom: max(40px, env(safe-area-inset-bottom));
  text-align: center;
}

/* Logo/Title Section */
.main-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 24px;
  background: linear-gradient(135deg, #2AABEE 0%, #229ED9 100%);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 24px rgba(42, 171, 238, 0.25);
}

.main-logo svg {
  width: 48px;
  height: 48px;
  color: white;
}

.main-title {
  font-size: 32px;
  font-weight: 700;
  margin: 0 0 8px 0;
  color: var(--tg-theme-text-color);
  letter-spacing: -0.5px;
}

.main-subtitle {
  font-size: 15px;
  color: var(--tg-theme-hint-color);
  margin: 0 0 48px 0;
  max-width: 320px;
  line-height: 1.5;
}

/* Action Buttons */
.main-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
  max-width: 320px;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 100%;
  padding: 16px 24px;
  background-color: var(--tg-card-bg);
  border: 1px solid var(--tg-card-border);
  border-radius: var(--tg-border-radius);
  color: var(--tg-theme-text-color);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease;
  text-decoration: none;
  box-shadow: var(--tg-shadow);
}

.action-button:hover {
  background-color: var(--tg-theme-secondary-bg-color);
  transform: translateY(-2px);
  box-shadow: var(--tg-shadow-elevated);
}

.action-button:active {
  transform: translateY(0);
  box-shadow: var(--tg-shadow);
}

.action-button.primary {
  background: linear-gradient(135deg, #2AABEE 0%, #229ED9 100%);
  border: none;
  color: white;
}

.action-button.primary:hover {
  background: linear-gradient(135deg, #3BB5F0 0%, #2AABEE 100%);
}

.action-button.sweep {
  background: linear-gradient(135deg, #FF6B35 0%, #E84E0E 100%);
  border: none;
  color: white;
}

.action-button.sweep:hover {
  background: linear-gradient(135deg, #FF7F50 0%, #FF6B35 100%);
}

/* Button Icons */
.action-button-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.8;
}

/* Footer */
.main-footer {
  padding: 20px 24px;
  padding-bottom: max(20px, env(safe-area-inset-bottom));
  text-align: center;
  border-top: 1px solid var(--tg-card-border);
}

.main-footer p {
  margin: 0;
  font-size: 13px;
  color: var(--tg-theme-hint-color);
}

.main-footer a {
  color: var(--tg-theme-accent-color);
  text-decoration: none;
}

.main-footer a:hover {
  text-decoration: underline;
}

/* Responsive ‚Äî –ø–ª–∞–Ω—à–µ—Ç—ã –∏ —É–∑–∫–∏–µ —ç–∫—Ä–∞–Ω—ã */
@media (max-width: 768px) {
  .main-header {
    padding-left: max(16px, env(safe-area-inset-left));
    padding-right: max(16px, env(safe-area-inset-right));
  }

  .main-content {
    padding: 32px 20px;
    padding-bottom: max(32px, env(safe-area-inset-bottom));
  }

  .main-title {
    font-size: 28px;
  }

  .main-subtitle {
    font-size: 14px;
    margin-bottom: 40px;
  }

  .main-actions {
    max-width: 100%;
  }

  .action-button {
    padding: 18px 24px;
    min-height: 48px;
  }
}

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —Ç–µ–ª–µ—Ñ–æ–Ω—ã */
@media (max-width: 480px) {
  .main-header {
    padding: 10px 12px;
    padding-top: max(10px, env(safe-area-inset-top));
    padding-left: max(12px, env(safe-area-inset-left));
    padding-right: max(12px, env(safe-area-inset-right));
  }

  .readme-link {
    padding: 10px 12px;
    font-size: 13px;
    min-height: 44px;
  }

  .header-right {
    gap: 8px;
  }

  .main-content {
    padding: 24px 16px;
    padding-bottom: max(24px, env(safe-area-inset-bottom));
  }

  .main-logo {
    width: 64px;
    height: 64px;
    margin-bottom: 16px;
  }

  .main-logo svg {
    width: 36px;
    height: 36px;
  }

  .main-title {
    font-size: 24px;
  }

  .main-subtitle {
    font-size: 14px;
    margin-bottom: 32px;
    max-width: 100%;
  }

  .main-actions {
    gap: 10px;
  }

  .action-button {
    padding: 16px 20px;
    font-size: 15px;
    min-height: 48px;
  }

  .main-footer {
    padding: 16px 12px;
    padding-bottom: max(16px, env(safe-area-inset-bottom));
  }

  .main-footer p {
    font-size: 12px;
  }
}

/* –û—á–µ–Ω—å —É–∑–∫–∏–µ —ç–∫—Ä–∞–Ω—ã */
@media (max-width: 360px) {
  .main-title {
    font-size: 22px;
  }

  .action-button {
    padding: 14px 16px;
    font-size: 14px;
  }
}

/* Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.main-content > * {
  animation: fadeInUp 0.4s ease-out;
}

.main-content > *:nth-child(2) {
  animation-delay: 0.1s;
}

.main-content > *:nth-child(3) {
  animation-delay: 0.2s;
}

.main-actions .action-button:nth-child(1) {
  animation: fadeInUp 0.4s ease-out 0.2s both;
}

.main-actions .action-button:nth-child(2) {
  animation: fadeInUp 0.4s ease-out 0.3s both;
}

.main-actions .action-button:nth-child(3) {
  animation: fadeInUp 0.4s ease-out 0.4s both;
}

.main-actions .action-button:nth-child(4) {
  animation: fadeInUp 0.4s ease-out 0.5s both;
}
