/* Telegram-style CSS Variables */
:root {
  --tg-viewport-height: 100vh;

  /* Light theme */
  --tg-theme-bg-color: #ffffff;
  --tg-theme-secondary-bg-color: #f1f1f1;
  --tg-theme-text-color: #000000;
  --tg-theme-hint-color: #707579;
  --tg-theme-link-color: #2AABEE;
  --tg-theme-button-color: #2AABEE;
  --tg-theme-button-text-color: #ffffff;
  --tg-theme-accent-color: #2AABEE;
  --tg-theme-destructive-color: #ff3b30;
  --tg-theme-success-color: #34c759;
  --tg-theme-warning-color: #ff9500;
  
  /* Additional variables */
  --tg-header-bg: #ffffff;
  --tg-card-bg: #ffffff;
  --tg-card-border: rgba(0, 0, 0, 0.08);
  --tg-input-bg: #f1f1f1;
  --tg-input-border: #e0e0e0;
  --tg-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  --tg-shadow-elevated: 0 4px 12px rgba(0, 0, 0, 0.08);
  --tg-border-radius: 12px;
  --tg-border-radius-lg: 16px;
}

/* Dark theme */
@media (prefers-color-scheme: dark) {
  :root {
    --tg-theme-bg-color: #18222d;
    --tg-theme-secondary-bg-color: #232e3c;
    --tg-theme-text-color: #ffffff;
    --tg-theme-hint-color: #8b9198;
    --tg-theme-link-color: #6ab2f2;
    --tg-theme-button-color: #2AABEE;
    --tg-theme-button-text-color: #ffffff;
    --tg-theme-accent-color: #6ab2f2;
    --tg-theme-destructive-color: #ff453a;
    --tg-theme-success-color: #30d158;
    --tg-theme-warning-color: #ff9f0a;
    
    --tg-header-bg: #18222d;
    --tg-card-bg: #232e3c;
    --tg-card-border: rgba(255, 255, 255, 0.08);
    --tg-input-bg: #293a4c;
    --tg-input-border: #3d4f61;
    --tg-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    --tg-shadow-elevated: 0 4px 12px rgba(0, 0, 0, 0.3);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Nunito', 'Bpmf Huninn', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  line-height: 1.5;
  transition: background-color 0.2s, color 0.2s;
  /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–µ–Ω–∏–µ –∑—É–º–∞ –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ –Ω–∞ input –≤ iOS */
  -webkit-text-size-adjust: 100%;
}

button, input, textarea, select {
  font-family: inherit;
}

code {
  font-family: 'SF Mono', SFMono-Regular, Consolas, 'Liberation Mono', Menlo, monospace;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--tg-theme-hint-color);
  border-radius: 3px;
  opacity: 0.5;
}

::-webkit-scrollbar-thumb:hover {
  opacity: 0.8;
}

/* Selection styling */
::selection {
  background: var(--tg-theme-accent-color);
  color: var(--tg-theme-button-text-color);
}
