/* Telegram-style App Container */
.App {
  min-height: var(--tg-viewport-height, 100vh);
  display: flex;
  flex-direction: column;
  background-color: var(--tg-theme-bg-color);
}

/* Page transitions */
.page-enter {
  opacity: 0;
  transform: translateY(10px);
}

.page-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
}

.page-exit {
  opacity: 1;
  transform: translateY(0);
}

.page-exit-active {
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.2s ease-in, transform 0.2s ease-in;
}

/* Global button styles */
.tg-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 24px;
  border: none;
  border-radius: var(--tg-border-radius);
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease;
  text-decoration: none;
  white-space: nowrap;
}

.tg-button-primary {
  background-color: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
}

.tg-button-primary:hover:not(:disabled) {
  filter: brightness(1.1);
  transform: translateY(-1px);
}

.tg-button-primary:active:not(:disabled) {
  filter: brightness(0.95);
  transform: translateY(0);
}

.tg-button-secondary {
  background-color: var(--tg-theme-secondary-bg-color);
  color: var(--tg-theme-text-color);
}

.tg-button-secondary:hover:not(:disabled) {
  background-color: var(--tg-input-bg);
}

.tg-button-danger {
  background-color: var(--tg-theme-destructive-color);
  color: var(--tg-theme-button-text-color);
}

.tg-button-danger:hover:not(:disabled) {
  filter: brightness(1.1);
}

.tg-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Global input styles */
.tg-input {
  width: 100%;
  padding: 12px 16px;
  background-color: var(--tg-input-bg);
  border: 1px solid var(--tg-input-border);
  border-radius: var(--tg-border-radius);
  font-size: 15px;
  color: var(--tg-theme-text-color);
  transition: border-color 0.2s, box-shadow 0.2s;
}

.tg-input:focus {
  outline: none;
  border-color: var(--tg-theme-accent-color);
  box-shadow: 0 0 0 3px rgba(42, 171, 238, 0.15);
}

.tg-input::placeholder {
  color: var(--tg-theme-hint-color);
}

/* Card styles */
.tg-card {
  background-color: var(--tg-card-bg);
  border-radius: var(--tg-border-radius-lg);
  border: 1px solid var(--tg-card-border);
  box-shadow: var(--tg-shadow);
  overflow: hidden;
  transition: box-shadow 0.2s, transform 0.2s;
}

.tg-card:hover {
  box-shadow: var(--tg-shadow-elevated);
}

/* Glass effect (for overlays and special elements) */
.tg-glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

@media (prefers-color-scheme: dark) {
  .tg-glass {
    background: rgba(35, 46, 60, 0.8);
  }
}

/* Loading spinner */
.tg-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid var(--tg-theme-secondary-bg-color);
  border-top-color: var(--tg-theme-accent-color);
  border-radius: 50%;
  animation: tg-spin 0.8s linear infinite;
}

@keyframes tg-spin {
  to {
    transform: rotate(360deg);
  }
}

/* Badge styles */
.tg-badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tg-badge-primary {
  background-color: var(--tg-theme-accent-color);
  color: var(--tg-theme-button-text-color);
}

.tg-badge-success {
  background-color: var(--tg-theme-success-color);
  color: var(--tg-theme-button-text-color);
}

.tg-badge-warning {
  background-color: var(--tg-theme-warning-color);
  color: var(--tg-theme-button-text-color);
}

.tg-badge-danger {
  background-color: var(--tg-theme-destructive-color);
  color: var(--tg-theme-button-text-color);
}

/* Responsive */
@media (max-width: 768px) {
  .tg-button {
    padding: 14px 20px;
    font-size: 16px;
  }

  .tg-input {
    padding: 14px 16px;
    font-size: 16px;
  }
}
