/* Telegram-style Create Contract Page */
.create-contract {
  min-height: 100vh;
  min-height: 100dvh;
  background-color: var(--tg-theme-bg-color);
  transition: background-color 0.2s;
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}
.small-text{
  font-size: 12px;
  color: var(--tg-theme-hint-color);
  margin-top: 4px;
  margin-bottom: 4px;
  line-height: 1.4;
  font-weight: 400;
  text-align: left;
}
.create-contract-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: var(--tg-theme-hint-color);
}

.create-contract-loading::after {
  content: '';
  width: 24px;
  height: 24px;
  margin-left: 12px;
  border: 2px solid var(--tg-theme-secondary-bg-color);
  border-top-color: var(--tg-theme-accent-color);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Header */
.create-contract-header {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  padding-top: max(12px, env(safe-area-inset-top));
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
  background-color: var(--tg-header-bg);
  border-bottom: 1px solid var(--tg-card-border);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.back-button {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: transparent;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: var(--tg-theme-accent-color);
  transition: all 0.15s ease;
}

.back-button:hover {
  background-color: var(--tg-theme-secondary-bg-color);
}

.back-button:focus-visible {
  outline: 2px solid var(--tg-theme-accent-color);
  outline-offset: 2px;
}

.create-contract-header h1 {
  margin: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 18px;
  font-weight: 600;
  color: var(--tg-theme-text-color);
}

.paper {
  background-color: var(--tg-card-bg);
  border: 1px solid var(--tg-card-border);
  border-radius: var(--tg-border-radius-lg);
  padding: 16px;
  box-shadow: var(--tg-shadow);
  margin-bottom: 20px;
}

.paper .create-contract-description {
  margin: 0;
  font-size: 14px;
  line-height: 1.5;
  color: var(--tg-theme-hint-color);
}

/* Content */
.create-contract-content {
  max-width: 520px;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: max(20px, env(safe-area-inset-bottom));
}

/* Form */
.contract-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Token Selection */
.token-selection {
  background-color: var(--tg-card-bg);
  border: 1px solid var(--tg-card-border);
  border-radius: var(--tg-border-radius-lg);
  padding: 16px;
  box-shadow: var(--tg-shadow);
}

.token-selection .form-label {
  margin-bottom: 12px;
  font-weight: 700;
  color: var(--tg-theme-text-color);
}

.token-buttons {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
}

.token-button {
  flex: 1;
  min-width: 80px;
  padding: 10px 16px;
  background-color: var(--tg-theme-secondary-bg-color);
  border: 2px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: var(--tg-theme-text-color);
  transition: all 0.15s ease;
}

.token-button:hover {
  border-color: var(--tg-theme-accent-color);
}

.token-button.active {
  background: linear-gradient(135deg, #2AABEE 0%, #229ED9 100%);
  color: white;
  border-color: transparent;
}

/* Form Labels and Inputs */
.form-label {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 14px;
  font-weight: 700;
  color: var(--tg-theme-text-color);
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  background-color: var(--tg-input-bg);
  border: 1px solid var(--tg-input-border);
  border-radius: var(--tg-border-radius);
  font-size: 16px;
  font-family: inherit;
  color: var(--tg-theme-text-color);
  transition: all 0.15s ease;
}

.form-input:focus {
  outline: none;
  border-color: var(--tg-theme-accent-color);
  box-shadow: 0 0 0 3px rgba(42, 171, 238, 0.15);
}

.form-input::placeholder {
  color: var(--tg-theme-hint-color);
}

/* Remove number input spinners */
.form-input[type="number"]::-webkit-outer-spin-button,
.form-input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-input[type="number"] {
  -moz-appearance: textfield;
}

.form-hint {
  display: block;
  margin-top: 6px;
  color: var(--tg-theme-hint-color);
  font-size: 13px;
  font-weight: 400;
}

.form-error {
  display: block;
  margin-top: 6px;
  color: #e53935;
  font-size: 12px;
  font-weight: 400;
}

/* Metadata Loading/Info */
.metadata-loading {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 12px;
  padding: 10px 12px;
  background-color: var(--tg-theme-secondary-bg-color);
  border-radius: 8px;
  color: var(--tg-theme-hint-color);
  font-size: 13px;
}

.metadata-info {
  margin-top: 12px;
  padding: 12px;
  background-color: rgba(52, 199, 89, 0.1);
  border: 1px solid rgba(52, 199, 89, 0.2);
  border-radius: 10px;
  font-size: 14px;
  color: var(--tg-theme-text-color);
}

.metadata-info strong {
  color: var(--tg-theme-success-color);
}

/* Error Message */
.error-message {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  margin-top: 16px;
  background-color: rgba(255, 59, 48, 0.1);
  border: 1px solid rgba(255, 59, 48, 0.2);
  border-radius: var(--tg-border-radius);
  color: var(--tg-theme-destructive-color);
  font-size: 14px;
}

.error-message::before {
  content: '!';
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  background-color: var(--tg-theme-destructive-color);
  color: white;
  border-radius: 50%;
  font-size: 12px;
  font-weight: 700;
  flex-shrink: 0;
}

/* Create Button */
.create-button {
  width: 100%;
  margin-top: 24px;
  padding: 16px 24px;
  background: linear-gradient(135deg, #2AABEE 0%, #229ED9 100%);
  border: none;
  border-radius: var(--tg-border-radius);
  font-size: 16px;
  font-weight: 600;
  color: white;
  cursor: pointer;
  transition: all 0.15s ease;
  box-shadow: 0 4px 12px rgba(42, 171, 238, 0.3);
}

.create-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(42, 171, 238, 0.4);
}

.create-button:active:not(:disabled) {
  transform: translateY(0);
}

.create-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  box-shadow: none;
}

/* Responsive ‚Äî –ø–ª–∞–Ω—à–µ—Ç—ã */
@media (max-width: 768px) {
  .create-contract-content {
    padding: 16px;
    padding-bottom: max(16px, env(safe-area-inset-bottom));
  }

  .token-button {
    min-height: 44px;
  }

  .form-input {
    font-size: 16px;
    padding: 16px;
  }

  .create-button {
    min-height: 48px;
  }
}

/* –ú–æ–±–∏–ª—å–Ω—ã–µ —Ç–µ–ª–µ—Ñ–æ–Ω—ã */
@media (max-width: 480px) {
  .create-contract-header {
    padding: 10px 12px;
    padding-top: max(10px, env(safe-area-inset-top));
    padding-left: max(12px, env(safe-area-inset-left));
    padding-right: max(12px, env(safe-area-inset-right));
  }

  .create-contract-header h1 {
    font-size: 16px;
  }

  .back-button {
    padding: 10px 12px;
    min-height: 44px;
  }

  .create-contract-content {
    padding: 12px;
    padding-bottom: max(12px, env(safe-area-inset-bottom));
  }

  .contract-form {
    gap: 16px;
  }

  .token-selection {
    padding: 12px;
  }

  .token-button {
    padding: 12px 16px;
    min-height: 48px;
  }

  .form-input {
    padding: 14px 16px;
    min-height: 48px;
  }

  .create-button {
    padding: 14px 20px;
    min-height: 48px;
    margin-top: 20px;
  }

  .error-message {
    padding: 10px 12px;
    font-size: 13px;
  }
}

/* –û—á–µ–Ω—å —É–∑–∫–∏–µ —ç–∫—Ä–∞–Ω—ã */
@media (max-width: 360px) {
  .create-contract-content {
    padding: 10px;
  }

  .token-selection {
    padding: 10px;
  }
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.contract-form > * {
  animation: fadeIn 0.3s ease-out;
}

.contract-form > *:nth-child(1) { animation-delay: 0.05s; }
.contract-form > *:nth-child(2) { animation-delay: 0.1s; }
.contract-form > *:nth-child(3) { animation-delay: 0.15s; }
.contract-form > *:nth-child(4) { animation-delay: 0.2s; }
.contract-form > *:nth-child(5) { animation-delay: 0.25s; }
