/* Readme page */
.readme-page {
  min-height: 100vh;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  background-color: var(--tg-theme-bg-color);
  transition: background-color 0.2s;
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}

.readme-header {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  padding-top: max(12px, env(safe-area-inset-top));
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
  background-color: var(--tg-header-bg);
  border-bottom: 1px solid var(--tg-card-border);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.readme-back-button {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: transparent;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: var(--tg-theme-accent-color);
  transition: all 0.15s ease;
}

.readme-back-button:hover {
  background-color: var(--tg-theme-secondary-bg-color);
}

.readme-header h1 {
  margin: 0;
  margin-left: auto;
  font-size: 18px;
  font-weight: 600;
  color: var(--tg-theme-text-color);
}

.readme-content {
  flex: 1;
  padding: 24px 20px 40px;
  padding-bottom: max(40px, env(safe-area-inset-bottom));
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  color: var(--tg-theme-text-color);
}

.readme-content h2 {
  font-size: 22px;
  font-weight: 700;
  margin: 0 0 12px 0;
  color: var(--tg-theme-text-color);
}

.readme-content h3 {
  font-size: 16px;
  font-weight: 600;
  margin: 24px 0 8px 0;
  color: var(--tg-theme-text-color);
}

.readme-content p {
  font-size: 15px;
  line-height: 1.6;
  margin: 0 0 12px 0;
  color: var(--tg-theme-hint-color);
}

.readme-content ul {
  margin: 0 0 12px 0;
  padding-left: 20px;
}

.readme-content li {
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 8px;
  color: var(--tg-theme-hint-color);
}

.readme-content a {
  color: var(--tg-theme-accent-color);
  text-decoration: none;
}

.readme-content a:hover {
  text-decoration: underline;
}

.readme-footer-link {
  margin-top: 32px !important;
  font-size: 14px !important;
}

/* –ú–æ–±–∏–ª—å–Ω–∞—è –∞–¥–∞–ø—Ç–∞—Ü–∏—è Readme */
@media (max-width: 480px) {
  .readme-header {
    padding: 10px 12px;
    padding-top: max(10px, env(safe-area-inset-top));
    padding-left: max(12px, env(safe-area-inset-left));
    padding-right: max(12px, env(safe-area-inset-right));
  }

  .readme-header h1 {
    font-size: 16px;
  }

  .readme-back-button {
    padding: 10px 12px;
    min-height: 44px;
  }

  .readme-content {
    padding: 16px 12px 24px;
    padding-bottom: max(24px, env(safe-area-inset-bottom));
  }

  .readme-content h2 {
    font-size: 20px;
  }

  .readme-content h3 {
    font-size: 15px;
  }

  .readme-content p,
  .readme-content li {
    font-size: 14px;
  }

  .readme-content ul {
    padding-left: 18px;
  }
}
