.sweep-page {
  min-height: 100vh;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  background-color: var(--tg-theme-bg-color);
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}

.sweep-header {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  padding-top: max(12px, env(safe-area-inset-top));
  background-color: var(--tg-header-bg);
  border-bottom: 1px solid var(--tg-card-border);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  gap: 12px;
}

.sweep-header h1 {
  flex: 1;
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--tg-theme-text-color);
}

.sweep-header .back-button {
  display: flex;
  align-items: center;
  gap: 4px;
  background: transparent;
  border: none;
  color: var(--tg-theme-accent-color);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  padding: 8px 4px;
  font-family: inherit;
}

.refresh-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: transparent;
  border: none;
  color: var(--tg-theme-accent-color);
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.15s;
}

.refresh-button:hover {
  background-color: var(--tg-theme-secondary-bg-color);
}

.refresh-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.sweep-content {
  flex: 1;
  padding: 16px 20px;
  padding-bottom: max(20px, env(safe-area-inset-bottom));
}

.sweep-error {
  padding: 12px 16px;
  background-color: rgba(255, 59, 48, 0.1);
  border: 1px solid rgba(255, 59, 48, 0.2);
  border-radius: var(--tg-border-radius);
  color: #FF3B30;
  font-size: 14px;
  margin-bottom: 16px;
}

.sweep-add-manual {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

.sweep-input {
  flex: 1;
  padding: 10px 12px;
  background-color: var(--tg-theme-secondary-bg-color);
  border: 1px solid var(--tg-card-border);
  border-radius: 8px;
  color: var(--tg-theme-text-color);
  font-size: 13px;
  font-family: 'SF Mono', 'Fira Code', monospace;
  outline: none;
  transition: border-color 0.15s;
  min-width: 0;
}

.sweep-input:focus {
  border-color: var(--tg-theme-accent-color);
}

.sweep-input::placeholder {
  color: var(--tg-theme-hint-color);
  opacity: 0.6;
}

.sweep-add-button {
  padding: 10px 16px;
  background: var(--tg-theme-accent-color);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
  font-family: inherit;
  transition: opacity 0.15s;
}

.sweep-add-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.sweep-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  gap: 16px;
  color: var(--tg-theme-hint-color);
}

.sweep-loading small {
  font-size: 12px;
  opacity: 0.7;
}

.sweep-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid var(--tg-card-border);
  border-top-color: var(--tg-theme-accent-color);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

.sweep-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  gap: 12px;
  text-align: center;
  color: var(--tg-theme-hint-color);
}

.sweep-empty svg {
  opacity: 0.4;
}

.sweep-empty p {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
  color: var(--tg-theme-text-color);
}

.sweep-empty small {
  font-size: 13px;
}

.sweep-summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 0;
  margin-bottom: 12px;
  border-bottom: 1px solid var(--tg-card-border);
}

.sweep-summary span {
  font-size: 14px;
  font-weight: 500;
  color: var(--tg-theme-hint-color);
}

.sweep-all-button {
  padding: 8px 16px;
  background: linear-gradient(135deg, #FF6B35 0%, #E84E0E 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: inherit;
}

.sweep-all-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(232, 78, 14, 0.3);
}

.sweep-all-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.sweep-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.sweep-card {
  background-color: var(--tg-card-bg);
  border: 1px solid var(--tg-card-border);
  border-radius: var(--tg-border-radius);
  padding: 16px;
  box-shadow: var(--tg-shadow);
}

.sweep-card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}

.sweep-card-name {
  font-size: 15px;
  font-weight: 600;
  color: var(--tg-theme-text-color);
}

.sweep-card-badge {
  padding: 2px 8px;
  background-color: rgba(255, 59, 48, 0.1);
  color: #FF3B30;
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.sweep-card-details {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 12px;
}

.sweep-card-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sweep-card-label {
  font-size: 13px;
  color: var(--tg-theme-hint-color);
}

.sweep-card-value {
  font-size: 13px;
  color: var(--tg-theme-text-color);
  font-weight: 500;
  font-family: 'SF Mono', 'Fira Code', monospace;
}

.sweep-card-expired {
  color: #FF3B30;
}

.sweep-card-button {
  width: 100%;
  padding: 10px;
  background: linear-gradient(135deg, #FF6B35 0%, #E84E0E 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: inherit;
}

.sweep-card-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(232, 78, 14, 0.3);
}

.sweep-card-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

@media (max-width: 480px) {
  .sweep-header {
    padding: 10px 12px;
    padding-top: max(10px, env(safe-area-inset-top));
  }

  .sweep-content {
    padding: 12px;
    padding-bottom: max(12px, env(safe-area-inset-bottom));
  }

  .sweep-card {
    padding: 12px;
  }
}
